.content {
    width: 1180px;
    margin: 17px auto;
    /* margin-bottom: 34px; */
    display: grid;
    row-gap: 30px;
    text-align: center;
}

.box {
    display: grid;
    grid-template-columns: 1fr 1fr;
    column-gap: 30px;
    align-items: center;
}

.boxMap {
    display: grid;
    justify-content: end;
}