.content {
    display: grid;
    grid-template-columns: auto auto;
    grid-template-rows: auto auto;
}

.item {
    display: grid;
    justify-content: start;
    align-content: center;
    padding-left: 25px;
    z-index: 2;
    text-transform: uppercase;
    line-height: 1.3;
    grid-column: 1;
    font-weight: 500;
    height: 100%;
    font-size: 40px;
    grid-row: 2/3;
    grid-template-columns: 2;
    grid-template-rows: 1;
}

.item_text {
    display: grid;
    grid-column: 1;
    grid-row: 1;
    align-items: center;
}

.border {
    display: grid;
    height: auto;
    width: 3px;
    grid-column: 2;
    grid-row: 1;
    background-color: #AE0018;
    margin-left: 34px;
    margin-top: 12px;
    margin-bottom: 11px;
    /* justify-self: end; */
    /* z-index: 3; */
}

.btn {
    display: grid;
    /* grid-column: 1/3; */
    grid-column: 1;
    grid-row: 1;
    /* margin-top: 7px; */
    /* margin-bottom: 11px; */
    justify-self: end;
    align-self: end;
    margin-bottom: 11px;
}