.content {
    display: grid;
    /* grid-row: 2fr 1fr; */
    text-decoration: none;
    margin-bottom: 17px;

}

.headerBox {
    display: flex;
    /* margin-top: 17px; */
    padding-top: 17px;
    padding-bottom: -6px;
    /* width: 1200px; */
    margin: auto;
    justify-self: center;
}

.itemBox {
    display: grid;
    /* grid-row: 2/3; */
    grid-template-columns: 1fr 1fr;
    margin-top: 19px;
    margin-bottom: 25px;
    grid-column-gap: 28px;
}

.itemBoxLeft {
    display: grid;
    grid-row-gap: 13px;
    grid-template-rows: 1fr 1fr;
    justify-content: end;
    align-items: end;
}

.itemBoxRight {
    display: grid;
    grid-row-gap: 13px;
    grid-template-rows: 1fr 1fr;
    justify-content: start;
    align-items: end;
}

.logo {
    margin-top: 17px;
    justify-self: center;
    height: 30px;
}

.slider {
    z-index: 5;
}

.contentBox {
    display: grid;
    justify-items: center;

    /* background-color: #ECEEF2; */
    background-color: #eceef274;
    box-shadow: 2px 2px 3px #9090904b;
}

.textBox {
    padding-left: 25px;
    display: grid;
    grid-template-columns: 80% 20%;
    align-items: start;
    justify-items: center;
    z-index: 1;
    margin-bottom: 40px;
    margin-top: 40px;
    font-size: 26px;
    text-align: left;
}

