.itemBox {
    display: grid;
    grid-template-rows: 222px 58px;
    width: 410px;
    box-shadow: 0 0 5px 0 rgba(0, 0, 0, .3);
    border-radius: 18px 18px 6px 6px;
    transition: all .8s cubic-bezier(.25, .8, .25, 1);
}

.itemBox:hover {

    transition: box-shadow .2s;
    box-shadow: 0 0 15px 2px rgba(0, 0, 0, .35);
    /* box-shadow: 5px 5px 7px #90909083; */
}

.itemBox:hover img {
    /* display: flex;
    object-fit: contain;
    object-position: center; */
    /* Центрирование изображения */
    /* justify-items: end; */
    transition: width .2s, height .2s;
    height: 99%;
    width: 99%;
}